import Vue from 'vue'
import Router from 'vue-router'
import Posts from '@/components/Posts'
import Shops from '@/components/Shops'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Posts
    },
    {
      path: '/khuyen-mai/:oid?/:s?/:page?',
      name: 'Posts',
      component: Posts
    },
    {
      path: '/nguon-tin',
      name: 'Shops',
      component: Shops
    }
  ],
  mode: 'history'
})



